/************ UIKit vars ************************/
/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-panel {
  z-index: 2000;
  position: fixed;
  resize: both;
  opacity: 0.95;
  direction: ltr;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: calc(var(--alpheios-base-text-size) * 3.5) auto calc(var(--alpheios-base-text-size) * 3.5);
  grid-template-areas: "header" "content" "content";
}

.alpheios-panel[data-notification-visible=true] {
  grid-template-areas: "header" "content" "notifications";
}

.alpheios-panel__header {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  box-sizing: border-box;
  grid-area: header;
  background: var(--alpheios-toolbar-bg-color);
}

.alpheios-panel__header-selection {
  font-size: 16px;
  font-weight: 700;
  color: #4E6476;
}

.alpheios-panel__header-word {
  font-size: 14px;
  position: relative;
  top: -1px;
}

.alpheios-panel__close-btn {
  width: calc(var(--alpheios-base-text-size) * 3.75);
  height: 100%;
  cursor: pointer;
  fill: var(--alpheios-icon-color);
  stroke: var(--alpheios-icon-color);
  stroke-width: 2.5;
}
.alpheios-panel__close-btn svg {
  position: relative;
  left: calc(var(--alpheios-base-text-size) * 0.5);
  width: calc(var(--alpheios-base-text-size) * 2.75);
  height: auto;
  top: 50%;
  transform: translateY(-50%);
}
.alpheios-panel__close-btn:hover, .alpheios-panel__close-btn:focus {
  fill: var(--alpheios-icon-color-hover);
  stroke: var(--alpheios-icon-color-hover);
  background: var(--alpheios-icon-bg-color-hover);
}
.alpheios-panel__close-btn:active {
  fill: var(--alpheios-icon-color-pressed);
  stroke: var(--alpheios-icon-color-pressed);
  background: var(--alpheios-icon-bg-color-pressed);
}

.alpheios-panel__content {
  overflow: auto;
  grid-area: content;
  direction: ltr;
  box-sizing: border-box;
  display: flex;
  flex-flow: wrap;
  position: relative;
  background: var(--alpheios-color-neutral-lightest);
  padding-top: calc(var(--alpheios-base-text-size) * 1.25);
}

.alpheios-panel__title {
  text-transform: capitalize;
}

.alpheios-lookup__panel {
  display: block;
  border-bottom: 1px solid;
  margin-bottom: 20px;
}

.alpheios-panel__notifications, .alpheios-panel_notifications-auth {
  display: none;
  position: relative;
  padding: 10px 20px;
  background: #BCE5F0;
  grid-area: notifications;
  overflow: hidden;
}

.alpheios-panel__notifications-close-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  display: block;
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-panel__notifications-close-btn:hover,
.alpheios-panel__notifications-close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}

.alpheios-panel__notifications--lang-switcher {
  font-size: 12px;
  float: right;
  margin: -20px 10px 0 0;
  display: inline-block;
}

.alpheios-panel__notifications--lang-switcher .alpheios-select {
  width: 120px;
  height: 25px;
}

.alpheios-panel__notifications--important {
  background: #73CDDE;
}

[data-notification-visible=true] .alpheios-panel__notifications {
  display: block;
}

[data-notification-auth-visible=true] .alpheios-panel__notifications-auth {
  display: block;
}

.alpheios-panel__tab-panel {
  display: flex;
  flex-direction: column;
  padding: 20px;
  width: 100%;
}

.alpheios-panel__tab-panel--fw {
  width: 100%;
}

.alpheios-panel__tab-panel--no-padding {
  padding: 0;
}

.alpheios-panel__content_no_top_padding {
  padding-top: 0;
}

.alpheios-panel__tab__inflections h1 {
  margin-bottom: calc(var(--alpheios-base-text-size) * 0.3125);
}

.alpheios-panel__message {
  margin-bottom: 0.5rem;
}

.alpheios-panel__contentitem {
  margin-bottom: 1em;
}

.alpheios-panel__header-btn-group--center {
  direction: ltr;
  display: flex;
  flex-wrap: nowrap;
  box-sizing: border-box;
  height: 100%;
}

.alpheios-panel__header-btn-group--end {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  box-sizing: border-box;
  height: 100%;
}

.alpheios-panel__tab__options {
  width: 100%;
  max-width: calc(var(--alpheios-base-ui-size) * 37.5);
}

.alpheios-panel__options-item {
  margin-bottom: calc(var(--alpheios-base-text-size) * 0.625);
  display: flex;
  align-items: flex-start;
}

.alpheios-panel__menu-btn {
  width: 40px;
  height: 40px;
  margin: 10px 10px 10px 30px;
  cursor: pointer;
}

.alpheios-panel__menu-icon {
  width: 40px;
  height: 40px;
  fill: #D1D1D0;
}

.alpheios-panel__menu-icon:hover,
.alpheios-panel__menu-icon:focus {
  fill: #5BC8DC;
}

.alpheios-panel--compact {
  height: 50vh;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid #D1D1D0;
}
.alpheios-panel--compact .alpheios-panel__content {
  overflow: auto;
}