/************ UIKit vars ************************/
/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-toolbar {
  background: transparent;
  position: fixed;
  top: 10px;
  right: 15px;
  z-index: 10000;
}
.alpheios-toolbar .alpheios-navbuttons__btn {
  width: calc(var(--alpheios-base-ui-size) * 2.75);
  height: calc(var(--alpheios-base-ui-size) * 2.75);
  margin: calc(var(--alpheios-base-ui-size) * 0.5) 0;
  box-sizing: border-box;
  position: relative;
  background-color: var(--alpheios-toolbar-bg-color);
  border: calc(var(--alpheios-base-ui-size) * 0.0625) solid var(--alpheios-border-color);
  border-radius: calc(var(--alpheios-base-ui-size) * 0.625);
}
.alpheios-toolbar .alph_tooltip {
  position: relative;
}
.alpheios-toolbar .alph_tooltip .tooltiptext {
  visibility: hidden;
  position: absolute;
  width: 120px;
  text-align: center;
  z-index: 1;
  opacity: 0;
  font-size: 12px;
  display: none;
  padding: 5px 0;
  border-radius: 6px;
  transition: opacity 0.6s ease 0s;
  background-color: #fcfcfa;
  color: #4e6476;
  border-width: 1px;
  border-style: solid;
  border-color: #4e6476;
  border-image: initial;
}
.alpheios-toolbar .alph_tooltip .alph_tooltip-left {
  top: 0;
  bottom: auto;
  right: 128%;
}
.alpheios-toolbar .alph_tooltip .alph_tooltip-left::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent #555;
}
.alpheios-toolbar .alph_tooltip-left {
  top: 5px;
  bottom: auto;
  right: 128%;
}
.alpheios-toolbar .alph_tooltip-left::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent #555;
}
.alpheios-toolbar .alph_tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
  display: inline;
}

.alpheios-toolbar__header {
  box-sizing: border-box;
  border: 1px solid var(--alpheios-border-color);
  border-bottom-left-radius: calc(var(--alpheios-base-ui-size) * 0.625);
  border-bottom-right-radius: calc(var(--alpheios-base-ui-size) * 0.625);
  cursor: pointer;
  background: #FFF;
}
.alpheios-toolbar__header svg {
  width: calc(var(--alpheios-base-ui-size) * 1.25);
  height: auto;
  position: relative;
  left: calc(var(--alpheios-base-ui-size) * 0.6875);
  top: calc(var(--alpheios-base-ui-size) * -0.3125);
  fill: var(--alpheios-border-color);
}
.alpheios-toolbar__header.expanded svg {
  top: calc(var(--alpheios-base-ui-size) * 0.3125);
}
.alpheios-toolbar__header:hover svg {
  fill: #5BC8DC;
}

.alpheios-toolbar__drag-handle {
  width: calc(var(--alpheios-base-ui-size) * 2.75);
  height: calc(var(--alpheios-base-ui-size) * 2.75);
  border-bottom: none;
  background: var(--alpheios-toolbar-bg-color);
  box-sizing: border-box;
  border-top-left-radius: calc(var(--alpheios-base-ui-size) * 0.625);
  border-top-right-radius: calc(var(--alpheios-base-ui-size) * 0.625);
}

.alpheios-toolbar__logo-icon {
  width: 60%;
  height: auto;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-45%, -45%);
}

.alpheios-toolbar__lookup-control {
  cursor: pointer;
  background: #FFF;
}
.alpheios-toolbar__lookup-control .alpheios-navbuttons__btn {
  margin: 0;
  border-radius: 0;
  border-bottom: none;
}

.alpheios-toolbar__lookup {
  display: flex;
  position: absolute;
  width: calc(var(--alpheios-base-ui-size) * 20.625);
  height: calc(var(--alpheios-base-ui-size) * 5.625);
  background: var(--alpheios-text-bg-color);
  left: calc(var(--alpheios-base-ui-size) * -20);
  top: 0;
  border: calc(var(--alpheios-base-ui-size) * 0.0625) solid var(--alpheios-border-color);
  border-top-left-radius: calc(var(--alpheios-base-ui-size) * 0.625);
  border-bottom-left-radius: calc(var(--alpheios-base-ui-size) * 0.625);
  border-right: none;
  box-sizing: border-box;
  padding: calc(var(--alpheios-base-ui-size) * 0.625);
  padding-right: calc(var(--alpheios-base-ui-size) * 1.25);
  z-index: -1;
}
.alpheios-toolbar__lookup .alpheios-lookup__form {
  justify-content: flex-end;
}
.alpheios-toolbar__lookup .alph_tooltip {
  display: inline-block;
}

.alpheios-toolbar__buttons {
  display: flex;
  flex-direction: column;
}