/************ UIKit vars ************************/
/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-popup {
  display: flex;
  flex-direction: column;
  min-width: 300px;
  min-height: 150px;
  z-index: 1000;
  position: fixed;
  left: 200px;
  top: 100px;
  box-sizing: border-box;
  overflow: auto;
  border: 1px solid var(--alpheios-border-color);
  border-radius: calc(var(--alpheios-base-ui-size) * 0.625);
}

.alpheios-popup__header {
  display: flex;
  justify-content: space-between;
  height: calc(var(--alpheios-base-text-size) * 3.5);
  background: var(--alpheios-toolbar-bg-color);
}

.alpheios-popup__logo {
  position: relative;
  left: calc(var(--alpheios-base-text-size) * 1);
  top: calc(var(--alpheios-base-text-size) * 1.125);
}
.alpheios-popup__logo svg {
  width: calc(var(--alpheios-base-text-size) * 1.75);
  height: auto;
}

.alpheios-popup__close-btn {
  width: calc(var(--alpheios-base-text-size) * 5);
  height: 100%;
  cursor: pointer;
  fill: var(--alpheios-icon-color);
  stroke: var(--alpheios-icon-color);
  stroke-width: 2.5;
}
.alpheios-popup__close-btn svg {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  left: calc(var(--alpheios-base-text-size) * 1);
  width: calc(var(--alpheios-base-text-size) * 2.75);
  height: auto;
}
.alpheios-popup__close-btn:hover, .alpheios-popup__close-btn:focus {
  fill: var(--alpheios-icon-color-hover);
  stroke: var(--alpheios-icon-color-hover);
  background: var(--alpheios-icon-bg-color-hover);
}
.alpheios-popup__close-btn:active {
  fill: var(--alpheios-icon-color-active);
  stroke: var(--alpheios-icon-color-active);
  background: var(--alpheios-icon-bg-color-active);
}

.alpheios-popup__body {
  display: flex;
  flex-direction: column;
  padding: calc(var(--alpheios-base-text-size) * 1.25);
  background: var(--alpheios-text-bg-color);
}

.alpheios-popup__toolbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: calc(var(--alpheios-base-text-size) * 0.625);
}

.alpheios-popup__toolbar-text {
  padding-right: calc(var(--alpheios-base-text-size) * 1.25);
}

.alpheios-popup__toolbar-text[lang=ara] {
  padding: 0px 20px 0px 20px;
  /* the arabic amiri font does not like the top padding */
}

h3.alpheios-popup__toolbar-selection {
  display: inline-block;
}

.alpheios-popup__toolbar-buttons {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.alpheios-popup__toolbar-button {
  margin: 0 0 calc(var(--alpheios-base-text-size) * 0.625) calc(var(--alpheios-base-text-size) * 0.625);
}

.alpheios-popup__content {
  padding: calc(var(--alpheios-base-text-size) * 1.25);
  border: 1px solid var(--alpheios-border-color);
  border-radius: calc(var(--alpheios-base-ui-size) * 0.625);
}

.alpheios-popup__providers {
  margin-left: calc(var(--alpheios-base-text-size) * 2.5);
}

.alpheios-popup__providers-title {
  font-weight: 700;
  color: var(--alpheios-color-neutral-dark);
}

a.alpheios-popup__providers-link {
  display: inline-block;
  margin-bottom: calc(var(--alpheios-base-text-size) * 0.375);
  font-weight: 700;
}

.alpheios-popup__definitions--placeholder {
  padding: calc(var(--alpheios-base-text-size) * 0.625) 0;
}

.alpheios-popup__providers-item {
  color: var(--alpheios-color-neutral-dark);
}

.alpheios-popup__notifications,
.alpheios-popup__notifications-auth {
  display: none;
  position: relative;
  padding: calc(var(--alpheios-base-text-size) * 0.625) calc(var(--alpheios-base-text-size) * 1.25);
  background: #BCE5F0;
  flex: 0 0 calc(var(--alpheios-base-text-size) * 3.75);
  overflow: hidden;
}

.alpheios-popup__notifications-close-btn {
  position: absolute;
  right: calc(var(--alpheios-base-text-size) * 0.3125);
  top: calc(var(--alpheios-base-text-size) * 0.3125);
  display: block;
  width: calc(var(--alpheios-base-text-size) * 1.25);
  height: calc(var(--alpheios-base-text-size) * 1.25);
  margin: 0;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}

[data-notification-visible=true] .alpheios-popup__notifications {
  display: block;
}

[data-notification-auth-visible=true] .alpheios-popup__notifications-auth {
  display: block;
}

.alpheios-popup__notifications--lang-switcher {
  font-size: 12px;
  float: right;
  margin: calc(var(--alpheios-base-text-size) * -1.25) calc(var(--alpheios-base-text-size) * 0.625) 0 0;
  display: inline-block;
}

.alpheios-popup__notifications--lang-switcher .alpheios-select {
  width: calc(var(--alpheios-base-text-size) * 7.5);
  height: calc(var(--alpheios-base-text-size) * 1.5625);
}

.alpheios-popup__notifications--important {
  background: #73CDDE;
}