/************ UIKit vars ************************/
/*
 Calculates a size of text elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-text-size` custom prop.
 */
/*
 Calculates a size of UI elements relative to the base size.
 Such elements can be scaled proportionally by changing the `--alpheios-base-ui-size` custom prop.
 */
.alpheios-user-auth__user-info-box {
  margin-top: 20px;
  display: flex;
  border-top: 1px solid #D1D1D0;
  flex-direction: column;
}

.alpheios-user-auth__user-info-item-box {
  display: flex;
  flex-direction: row;
  padding: 5px 10px;
  border-bottom: 1px solid #D1D1D0;
}

.alpheios-user-auth__user-info-item-name {
  flex: 1 1;
}

.alpheios-user-auth__user-info-item-value {
  font-weight: 700;
  flex: 1 1;
  color: #3E8D9C !important;
  text-align: right;
}

.alpheios-user-auth__notifications {
  display: none;
  position: relative;
  padding: 10px 20px;
  background: #73CDDE;
  flex: 0 0 60px;
  box-sizing: border-box;
  overflow: hidden;
}

.alpheios-user-auth__notifications-close-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  display: block;
  width: 20px;
  height: 20px;
  margin: 0;
  cursor: pointer;
  fill: #D1D1D0;
  stroke: #D1D1D0;
}

.alpheios-user-auth__notifications-close-btn:hover,
.alpheios-user-auth__notifications-close-btn:focus {
  fill: #5BC8DC;
  stroke: #5BC8DC;
}

[data-notification-visible=true] .alpheios-user-auth__notifications {
  display: block;
}